"use client";

import { useEffect, useRef, useState } from "react";

const BACKEND_URL =
  "https://hallucination.calmwave-93bbec10.brazilsouth.azurecontainerapps.io";

export default function CameraView() {
  const videoRef = useRef<HTMLVideoElement>(null);
  const overlayRef = useRef<HTMLCanvasElement>(null);
  const captureCanvasRef = useRef<HTMLCanvasElement | null>(null);

  const [error, setError] = useState<string | null>(null);
  const sendingRef = useRef(false);

  // 1ï¸âƒ£ Start camera
  useEffect(() => {
    async function startCamera() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: "user" },
          audio: false,
        });

        if (videoRef.current) {
          videoRef.current.srcObject = stream;
        }

        // canvas invisÃ­vel sÃ³ para captura
        captureCanvasRef.current = document.createElement("canvas");
      } catch {
        setError("Camera access denied or not available.");
      }
    }

    startCamera();

    return () => {
      if (videoRef.current?.srcObject) {
        const tracks = (videoRef.current.srcObject as MediaStream).getTracks();
        tracks.forEach((track) => track.stop());
      }
    };
  }, []);

  // 2ï¸âƒ£ Loop de captura
  useEffect(() => {
    const interval = setInterval(captureAndSend, 700);
    return () => clearInterval(interval);
  }, []);

  async function captureAndSend() {
    if (sendingRef.current) return;

    const video = videoRef.current;
    const captureCanvas = captureCanvasRef.current;
    if (!video || !captureCanvas) return;

    if (video.readyState < 2) return;
    if (video.videoWidth === 0) return;

    sendingRef.current = true;

    const w = video.videoWidth;
    const h = video.videoHeight;

    captureCanvas.width = w;
    captureCanvas.height = h;

    const ctx = captureCanvas.getContext("2d");
    if (!ctx) return;

    ctx.drawImage(video, 0, 0, w, h);

    const blob = await new Promise<Blob | null>((resolve) =>
      captureCanvas.toBlob(resolve, "image/jpeg", 0.6)
    );

    if (!blob) {
      sendingRef.current = false;
      return;
    }

    const formData = new FormData();
    formData.append("file", blob, "frame.jpg");

    try {
      const res = await fetch(`${BACKEND_URL}/detect`, {
        method: "POST",
        body: formData,
      });

      const data = await res.json();
      drawCircles(data.boxes || []);
    } catch {
      console.warn("Detection failed");
    } finally {
      sendingRef.current = false;
    }
  }

  function drawCircles(boxes: any[]) {
    const video = videoRef.current;
    const canvas = overlayRef.current;
    if (!video || !canvas) return;

    const w = video.videoWidth;
    const h = video.videoHeight;

    canvas.width = w;
    canvas.height = h;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    ctx.clearRect(0, 0, w, h);
    ctx.strokeStyle = "lime";
    ctx.lineWidth = 3;

    boxes.forEach((b) => {
      const x1 = b.x1 * w;
      const y1 = b.y1 * h;
      const x2 = b.x2 * w;
      const y2 = b.y2 * h;

      const cx = (x1 + x2) / 2;
      const cy = (y1 + y2) / 2;
      const radius = Math.max(x2 - x1, y2 - y1) / 2;

      ctx.beginPath();
      ctx.arc(cx, cy, radius, 0, Math.PI * 2);
      ctx.stroke();
    });
  }

  if (error) {
    return <p className="text-red-500">{error}</p>;
  }

  return (
    <div className="relative w-full max-w-md rounded-xl overflow-hidden shadow-lg">
      <video
        ref={videoRef}
        autoPlay
        playsInline
        muted
        className="w-full h-auto object-contain"
      />
      <canvas
        ref={overlayRef}
        className="absolute top-0 left-0 pointer-events-none"
      />
    </div>
  );
}
