"use client";

export default function VoiceAssistant() {
  function speakFast(text: string) {
  const utterance = new SpeechSynthesisUtterance(text);
  utterance.lang = "pt-BR"; // ou "en-US" para inglÃªs
  utterance.rate = 1.05;
  utterance.pitch = 1;
  window.speechSynthesis.speak(utterance);
}


  function startListening() {
    const SpeechRecognition =
      (window as any).SpeechRecognition ||
      (window as any).webkitSpeechRecognition;

    const recognition = new SpeechRecognition();
    recognition.lang = "pt-BR";
    recognition.start();

    recognition.onresult = async (e: any) => {
      const transcript = e.results[0][0].transcript;

      const res = await fetch(
        "https://hallucination.calmwave-93bbec10.brazilsouth.azurecontainerapps.io/voice",
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ text: transcript })
        }
      );

      const data = await res.json();
      if(data.answer) {
        speakFast(data.answer);
      }
    };
  }

  return (
    <button
      onClick={startListening}
      className="w-full p-3 bg-blue-600 rounded mt-4"
    >
      ðŸŽ™ Ask Lumi
    </button>
  );
}
